// src/components/SEO.js
import React from 'react';
import { Helmet } from 'react-helmet';

const SEO = ({ title, description, lang, meta = [] }) => {
  const defaultTitle = title || 'AB | Full Stack Developer';
  const defaultDescription = description || 'Welcome to my project portfolio!';
  const defaultLang = lang || 'en';

  return (
    <Helmet
      htmlAttributes={{ lang: defaultLang }}
      title={defaultTitle}
      meta={[
        {
          name: 'description',
          content: defaultDescription,
        },
        {
          property: 'og:title',
          content: defaultTitle,
        },
        {
          property: 'og:description',
          content: defaultDescription,
        },
        {
          property: 'og:type',
          content: 'website',
        },
        ...meta,
      ]}
    >
      {/* Additional elements like link tags can also be included here */}
    </Helmet>
  );
};

export default SEO;
